























import { Component, Vue } from 'vue-property-decorator';
import { ITestContent } from '@/types/interface';
import banner from '../../components/Banner.vue';
import testRecordPanel from '../../components/TestRecordPanel.vue';
import data from './TempRecordData';

@Component({
  components: {
    banner,
    testRecordPanel,
  },
})

export default class TestRecord extends Vue {
  flag = 'all';
  list = data;

  link() {
    this.$router.push('/member');
  }

  get filterList() {
    return this.flag === 'all' ? this.list : this.list.filter((d: ITestContent) => {
      if (this.flag === 'done') return d.isFin === '1';
      return d.isFin === '0';
    });
  }

  switchHandler(idx: string) {
    this.flag = idx;
  }

  select(id: string) {
    // TODO: access test page
    console.log(id);
  }
}
